import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { Storage } from '@ionic/storage';


import * as moment from 'moment';

@Component({
    selector: 'create-review',
    templateUrl: 'create-review.html'
})

export class CreateReviewModal {
    
    public job:any;
    public profile:any;
    public permission:string;
    public review:any;
    
    constructor(public platform: Platform, public params: NavParams, public modalCtrl: ModalController, public viewCtrl: ViewController, public storage: Storage, private alertCtrl: AlertController, public events: Events, private toastCtrl: ToastController) {
        this.job = this.params.data.job;
        this.profile = this.params.data.profile;
        this.permission = this.params.data.permission;
        
        this.review = {profile_id:this.profile.id, job_id:this.job.id, rating:5};
    }
    
    public create(){
        this.viewCtrl.dismiss(this.review);
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }

    
}






