import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { LoginPage } from '../pages/login/login';
import { SignupPage } from '../pages/signup/signup';
import { ProfilePage } from '../pages/profile/profile';
import { ContractorJobsPage } from '../pages/contractor-jobs/contractor-jobs';
import { CustomerJobsPage } from '../pages/customer-jobs/customer-jobs';

import { ContractorJobPage } from '../pages/contractor-job/contractor-job';
import { CustomerJobPage } from '../pages/customer-job/customer-job';

import { SettingsPage } from '../pages/settings/settings';

import {AppSettings} from './app.settings';

import { AuthenticationProvider } from '../providers/authentication/authentication';

import { Storage } from '@ionic/storage';

import { OneSignal } from '@ionic-native/onesignal';

import { JobProvider } from '../providers/job/job';

import { ProfileProvider } from '../providers/profile/profile';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
    @ViewChild(Nav) nav: Nav;

    rootPage: any = LoginPage;

    pages: Array<{title: string, component: any}>;
    
    public user: any;

    constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public authProvider: AuthenticationProvider, public storage: Storage, private oneSignal: OneSignal, private events:Events, private jobProvider: JobProvider, private profileProvider: ProfileProvider) {
        this.initializeApp();
        
        this.user = {profile:{}};
        // used for an example of ngFor and navigation
        this.pages = [
            { title: 'Jobs', component: CustomerJobsPage },
            { title: 'My Profile', component: ProfilePage },
            { title: 'Settings', component: SettingsPage }
        ];
        
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
                this.savePushId();
                this.authProvider.getUserData();
                if (user.permission === "contractor"){
                    this.pages[0].component = ContractorJobsPage;
                    this.rootPage = ContractorJobsPage;
                }
                else{
                    this.rootPage = CustomerJobsPage;
                }
                
            }
            this.splashScreen.hide();
        }).catch(() => {
            this.splashScreen.hide();
        });
        
        this.events.subscribe("user:retreived", (user) => {
            console.log(user);
            this.user = user;
        });
    }
    
    savePushId(){
        this.oneSignal.getIds().then((data) => {
            if (data.userId){
                //save
                this.authProvider.savePushId(data.userId);
            }
        }).catch((e) => {
            //console.log(e);
        });        
    }    

    initializeApp() {
        this.platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            this.statusBar.styleDefault();
            this.splashScreen.hide();
 
            
            if (this.platform.is('cordova')){
                

                
                this.oneSignal.startInit("c37e5d83-df30-4cb2-97a3-fdb45dcd08ed", "560659639701");

                this.oneSignal.handleNotificationOpened().subscribe((data) => {
                    if (!data.notification.isAppInFocus){
                        let pushData = data.notification.payload.additionalData;
                        data.notification.payload.additionalData["title"] = data.notification.payload.title;
                        this.events.publish("notification:received", pushData);
                        if (pushData.type === "job" && this.user.permission === "contractor"){
                            this.jobProvider.getJob(pushData.id).then((job) => {
                                this.nav.push(ContractorJobPage, {job:job});
                                
                            });
                        }
                        else if (pushData.type === "job" && this.user.permission === "customer"){
                            this.jobProvider.getJob(pushData.id).then((job) => {
                                this.nav.push(CustomerJobPage, {job:job});
                            });
                        }
                        else if (pushData.type === "profile"){
                            if (pushData.id){
                            this.profileProvider.getProfile(pushData.id).then((profile) => {
                                this.nav.push(ProfilePage, {profile:profile});
                            });
                            }
                            else{
                                setTimeout(() => {this.nav.push(ProfilePage, {});},500);                            
                            }
                        }                        
                    }
                });

                this.oneSignal.endInit();
            }            
            
        });
    }

    openPage(page) {
        // Reset the content nav to have just this page
        // we wouldn't want the back button to show in this scenario
        this.nav.setRoot(page.component);
    }
    
    logout(){
        this.authProvider.logout();            
        this.nav.setRoot(LoginPage);
        this.nav.popToRoot();    
    }
}



