import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { ContractorJobsPage } from '../contractor-jobs/contractor-jobs';
import { CustomerJobsPage } from '../customer-jobs/customer-jobs';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ProfileProvider } from '../../providers/profile/profile';
import {
    GoogleMaps,
    GoogleMap,
    GoogleMapsEvent,
    GoogleMapOptions,
    CameraPosition,
    MarkerOptions,
    Marker
} from '@ionic-native/google-maps';

import { Camera, CameraOptions } from '@ionic-native/camera';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';

import {AppSettings} from '../../app/app.settings';

/**
 * Generated class for the SignupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {

    public profile:any;
    public user:any;
    public stage:number;
    public map:GoogleMap;

    constructor(public navCtrl: NavController, public navParams: NavParams, public authProvider: AuthenticationProvider, public profileProvider: ProfileProvider, private alertCtrl: AlertController, private camera: Camera,private transfer: FileTransfer, private toastCtrl: ToastController) {
        
        this.profile = {avatar:"https://www.drupal.org/files/issues/default-avatar.png", radius:50};
        this.user = {email:"", password:"", permission:"customer", loading:false};
        this.stage = 1;
        
    }
    
    
    public nextStage(){
        
        
        if (this.stage === 2 && (!this.profile.email || !this.validateEmail(this.profile.email))){
            let alert = this.alertCtrl.create({
                title: 'Invalid Email',
                subTitle: 'A valid email is required to sign up.',
                buttons: ['Dismiss']
            });
            alert.present();            
            return;
        }
        else if (this.stage === 2 && this.user.permission === "customer"){
            this.stage +=2;
        }
        else{
            this.stage +=1;
        }
        
        
        
        if (this.stage === 3){
            this.loadMap();
        }
    }
    
    public changeStage(stage){
        this.stage = stage;
    }
    
    public selectUserType(userType){
        this.user.permission = userType;
    }
    
    public signup(){
        this.user.loading = true;
        this.user.error = "";
        this.user.email = this.profile.public_email;
        this.authProvider.register(this.user).then(() => {
            this.profileProvider.updateProfile(this.profile).then(() => {
                this.user.loading = false;
                this.authProvider.getUserData();
                this.navigateToJobs();
                
            }).catch(() => {
                this.user.loading = true;
            })
        }).catch((e) => {
        console.log(e);
            this.user.loading = false;
            if (e && e.error){
                for (let error of e.error.error.errors){
                    this.user.error = this.user.error + error + " ";
                }                
            }
            else{
                this.user.error = e.message;
            }

            
        })
  
    }
    
    private navigateToJobs(){
        if (this.user.permission === 'customer'){
            this.navCtrl.setRoot(CustomerJobsPage);
        }
        else if (this.user.permission === 'contractor'){
            this.navCtrl.setRoot(ContractorJobsPage);
        }        
    }
    
    private validateEmail(email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }
    
    private  loadMap() {
        
        //set default lat and lng as auckland city
        this.profile.latitude = -36.8626662;
        this.profile.longitude = -174.7253866;    
        
        console.log("loading");
        let mapOptions: GoogleMapOptions = {
            camera: {
                target: {
                    lat: -36.8626662,
                    lng: -174.7253866
                },
                zoom: 11,
                tilt: 30
            }
        };

        this.map = GoogleMaps.create('map', mapOptions);
        
        
        this.map.one(GoogleMapsEvent.MAP_READY).then(() => {
            this.map.addEventListener("click").subscribe((data) => {
                console.log(data);
                this.addMarker(data);
            })
        })
        
    }
    
    private addMarker(data){
        //set profile lat and lng
        
        this.profile.latitude = data.lat;
        this.profile.longitude = data.lng;
        
        //remove other markers
        this.map.clear();
        
        this.map.addMarker({
            icon: '#79b402',
            animation: 'DROP',
            position: {
              lat: this.profile.latitude,
              lng: this.profile.longitude
            }
        });        
        //add this marker
        //add radius circle
        
        this.map.addCircle({
            center: {lat: this.profile.latitude, lng: this.profile.longitude},
            radius: this.profile.radius * 100,
            strokeColor : '#79b402',
            strokeOpacity: 0.8,
            strokeWidth: 5,
            fillColor : '#79b402',
            fillOpacity: 0.35,            
        })
    }
    
    public updateRadius(){
        if (!this.map){return;}
        this.addMarker({lat:this.profile.latitude, lng:this.profile.longitude});
    }
    
    
    public changeAvatar(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
                
                let toast = this.toastCtrl.create({
                    message: 'Your image is being uploaded...',
                    duration: 3000,
                    position: 'bottom'
                });

                toast.present();                
                
                fileTransfer.upload(image, encodeURI(AppSettings.uploadUrl), options).then((data) => {
                    var response = JSON.parse(data.response);
                    if (response.success === true){
                        this.profile.avatar = response.data;
                    } 
                    else{
                        let alert = this.alertCtrl.create({
                            title: "Error",
                            subTitle: "There was a problem uploading your image",
                            message: JSON.stringify(response),
                            buttons: [
                                {
                                    text: 'Dismiss',
                                    role: 'cancel'
                                }
                            ]
                        });
                        alert.present();                         
                    }                      
                }, (err) => {
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your image",
                        message: err,
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();                    
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   
    }     

    ionViewDidLoad() {
      console.log('ionViewDidLoad SignupPage');
    }

}



