import { Component } from '@angular/core';
import { NavController, NavParams, ModalController, Events, AlertController } from 'ionic-angular';
import { CreateJobModal } from '../../modals/create-job/create-job';
import { EditJobModal } from '../../modals/edit-job/edit-job';

import { JobProvider } from '../../providers/job/job';

import { CustomerJobPage } from '../customer-job/customer-job';

import { ReviewProvider } from '../../providers/review/review';

import { CreateReviewModal } from '../../modals/create-review/create-review';

import * as moment from 'moment';

import { Storage } from '@ionic/storage';

import { NotificationProvider } from '../../providers/notification/notification';
/**
 * Generated class for the CustomerJobsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-customer-jobs',
  templateUrl: 'customer-jobs.html',
})
export class CustomerJobsPage {
    
    public properties:any;
    public completedJobs:Array<any>;
    public openJobs:Array<any>;
    public user:any;
    public notificationCount:number;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, private jobProvider: JobProvider, public events: Events, private reviewProvider: ReviewProvider, private alertCtrl: AlertController, public storage: Storage, public notificationProvider:NotificationProvider) {
        
        this.properties = {activeTab:'created', loading:false};
        this.completedJobs = [];
        this.openJobs = [];
        this.getJobs();
        
        this.notificationCount = 0;
        this.notificationProvider.getNotificationCount().then((data:number) => {
            this.notificationCount = data;
        });         
        
        this.events.subscribe("job:accepted", (data) => {
            this.getJobs();
        });
        this.events.subscribe("job:declined", (data) => {
            this.getJobs();
        });
        this.events.subscribe("job:completed", (data) => {
            this.getJobs();
        });   
        this.events.subscribe("job:reviewed", (data) => {
            this.getJobs();
        });                  
        
        this.user = {};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
            }
        });
        
        this.events.subscribe("notification:updatecount", (data) => {
            this.notificationProvider.getNotificationCount().then((data:number) => {
                this.notificationCount = data;
            });
        });         
           
    }
    
    private getJobs(){
        this.properties.loading = true;
        this.jobProvider.getCreatedJobs().then((jobs:Array<any>) => {
            console.log(jobs);
            this.properties.loading = false;
            this.openJobs = jobs["open"];
            this.completedJobs = jobs["completed"];
        }).catch(() => {
            this.properties.loading = false;
        })
    }
    
    doRefresh(refresher) {
        this.jobProvider.getCreatedJobs().then((jobs:Array<any>) => {
            this.openJobs = jobs["open"];
            this.completedJobs = jobs["completed"];
            refresher.complete();
        }).catch(() => {
            refresher.complete();
        });
        
    }    
    
    public openJob(ev, job){
        ev.stopPropagation();
        this.navCtrl.push(CustomerJobPage, {job:job})
    }    
    
    public getPrimaryImage(job){
        let images = JSON.parse(job.images);
        if (images.length > 0){
            return images[0];
        }
        return "http://icons.iconarchive.com/icons/paomedia/small-n-flat/256/house-icon.png";
    }
    
    public formatTime(dateTimeString){
        return moment(dateTimeString).toNow();
    }
    
    public formatTime2(dateTimeString){
        return moment(dateTimeString).fromNow();
    }    
    
    public openCreateJob(){
        let modal = this.modalCtrl.create(CreateJobModal); 
        modal.onDidDismiss(job => {
            if (job){
                this.openJobs.push(job);
                this.jobProvider.createJob(job).then((res) => {
                    console.log(res);
                    job.id = res["id"];
                }).catch((e) => {
                    console.log(e);
                })
                console.log(job);
            }
        })
        modal.present();        
    }    
    
    public openEditJob(ev, job, index){
        ev.stopPropagation();
        let modal = this.modalCtrl.create(EditJobModal, {job:job}); 
        modal.onDidDismiss(job => {
            if (job && job.deleted){
                this.openJobs.splice(index,1);
                this.jobProvider.deleteJob(job.id);
            }
            else if (job){
                console.log(job);
                console.log(this.openJobs[index]);
                Object.assign(this.openJobs[index], job);
                this.jobProvider.updateJob(job).then((res) => {
                    console.log(res);
                }).catch((e) => {
                    console.log(e);
                })
                console.log(job);
            }
        })        
        modal.present();        
    }    
    
    public requiresReview(job){
        if (!job.reviews || job.reviews.length < 1){
            return true;
        }
        for (let review of job.reviews){
            if (this.user && this.user.profile && parseInt(review.submitted_by_profile_id) === parseInt(this.user.profile.id)){
                return false;
            } 
        }          
        
        return true;
    }   
    
    public createReview(ev, job){
        ev.stopPropagation();
        let profile = {};
        for (let application of job.applications){
            if (application.status === "accepted"){
                profile = application.profile;
            } 
        }       
        let modal = this.modalCtrl.create(CreateReviewModal, {job:job, profile:profile,permission:"customer"}); 
        modal.onDidDismiss(review => {
            if (review){
                console.log(review);
                this.reviewProvider.createReview(review).then(() => {
                    this.events.publish("job:reviewed", {job:job});
                    let alert = this.alertCtrl.create({
                        title: 'Review Submitted',
                        message: 'Thanks for submitting a review. It will be shown publicly on their profile.',
                        buttons: [
                          {
                            text: 'Dismiss',
                            role: 'cancel',
                            handler: () => {
                              console.log('Cancel clicked');
                            }
                          }
                        ]
                      });
                      alert.present();                      
                }); 
            }
        })        
        modal.present();             
    }    

    ionViewDidLoad() {
      console.log('ionViewDidLoad CustomerJobsPage');
    }

}



