import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, Events, ModalController } from 'ionic-angular';

import * as moment from 'moment';

import { JobProvider } from '../../providers/job/job';

import { ProfilePage } from '../profile/profile';

import { Storage } from '@ionic/storage';

/**
 * Generated class for the JobPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-contractor-job',
  templateUrl: 'contractor-job.html',
})
export class ContractorJobPage {
    public job:any;
    public user:any;
    constructor(public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController, private jobProvider: JobProvider, public events: Events, public modalCtrl: ModalController, private storage:Storage) {
    
        this.job = navParams.data.job;
        this.job.imagesArray = JSON.parse(this.job.images);
        
        this.user = {};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
            }
        });
        
        console.log(this.job);         
            
    }
    
    
    public viewProfile(){
        if (this.job.profile){
            this.job.profile.permission = "customer";
            this.navCtrl.push(ProfilePage, {profile:this.job.profile});
        }
    }
    
    public viewDirections(){
        window.open("https://www.google.com/maps/dir/Current+Location/" + this.job.location, '_system');
    }
    
    public formatTime(dateTimeString){
        return moment(dateTimeString).format("dddd, MMMM Do YYYY, h:mm a");
    }
    
    public hasApplied(){
        if (!this.job.applications || this.job.applications.length < 1){return false;}
        if (this.job.applied){return true;}
        for(let application of this.job.applications){
            if (this.user && this.user.profile && application.profile_id === this.user.profile.id){
                return true;
            }
        }
        return false;
    }  
    
    public getApplicationStatus(){
        for(let application of this.job.applications){
            if (this.user && this.user.profile && application.profile_id === this.user.profile.id){
                return application.status;
            }
        }
    }    
    
    public apply(){
        let alert = this.alertCtrl.create({
          title: 'Confirm',
          subTitle: 'Are you sure you want to apply for this job?',
          message: 'If you are accepted you will be obligated to do this job at the preferred date and time of the customer for the listed price.',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            {
              text: 'Yes',
              handler: () => {
                    this.job.applications = this.job.applications ? this.job.applications : [];
                    this.job.applications.push({profile_id:this.user.profile.id, job_id:this.job.id, status:"pending"});
                    this.jobProvider.apply(this.job.id, this.user.profile.id).then(() => {
                        
                        let alert = this.alertCtrl.create({
                          title: 'Success',
                          subTitle: 'You have successfully applied for this job.',
                          message: 'You will either be accepted or declined the job by the customer. Goodluck!',
                          buttons: [
                            {
                              text: 'Dismiss',
                              role: 'cancel',
                              handler: () => {
                                console.log('Cancel clicked');
                              }
                            }
                          ]
                        });
                        alert.present();                          
                        this.events.publish("job:applied", {job:this.job});
                        
                    });
                  
              }
            }
          ]
        });
        alert.present();          
    }           

  ionViewDidLoad() {
    console.log('ionViewDidLoad JobPage');
  }

}



