import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';

import { FindAddressModal } from '../../modals/find-address/find-address';

import {AppSettings} from '../../app/app.settings';

import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult } from '@ionic-native/native-geocoder';

import * as moment from 'moment';

@Component({
    selector: 'create-job',
    templateUrl: 'create-job.html'
})

export class CreateJobModal {
    
    public job:any;
    
    constructor(public platform: Platform, public params: NavParams, public modalCtrl: ModalController, public viewCtrl: ViewController, private camera: Camera, public storage: Storage, private alertCtrl: AlertController, public events: Events,private transfer: FileTransfer, private toastCtrl: ToastController, private nativeGeocoder: NativeGeocoder) {
        this.job = {imagesArray:["http://icons.iconarchive.com/icons/paomedia/small-n-flat/256/house-icon.png"]};
    }
    
    public create(){
        console.log(this.job);
        this.job.images = JSON.stringify(this.job.imagesArray);
        this.viewCtrl.dismiss(this.job);
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }
    
    public openFindAddress(){
        let modal = this.modalCtrl.create(FindAddressModal, {location:this.job.location}); 
        modal.onDidDismiss(address => {
            if (address){
                console.log(address);
                this.job.location = address.description;
                this.nativeGeocoder.forwardGeocode(address.description)
                    .then((coordinates: NativeGeocoderForwardResult) => {
                        this.job.latitude = coordinates.latitude;
                        this.job.longitude = coordinates.longitude;
                        }).catch((error: any) => console.log(error));                
            }
        })        
        modal.present();        
    }    
    
    public openImageOptions(index){
        let alert = this.alertCtrl.create({
          title: 'Modify Image',
          cssClass: "button-only-alert",
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              },
              cssClass: "cancel-button"
            },
            {
              text: 'Edit',
              handler: () => {
                  this.updateImage(index);
              },
              cssClass: "edit-button"
            },
            {
              text: 'Delete',
              handler: () => {
                    this.job.imagesArray.splice(index,1);
              },
              cssClass: "remove-button"
            }
          ]
        });
        alert.present();        
    }
    
    
    public addImage(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
                
                let toast = this.toastCtrl.create({
                    message: 'Your image is being uploaded...',
                    duration: 3000,
                    position: 'bottom'
                });

                toast.present();                
                
                fileTransfer.upload(image, encodeURI(AppSettings.uploadUrl), options).then((data) => {
                    var response = JSON.parse(data.response);
                    if (response.success === true){
                        this.job.imagesArray.push(response.data);
                    } 
                    else{
                        let alert = this.alertCtrl.create({
                            title: "Error",
                            subTitle: "There was a problem uploading your image",
                            message: JSON.stringify(response),
                            buttons: [
                                {
                                    text: 'Dismiss',
                                    role: 'cancel'
                                }
                            ]
                        });
                        alert.present();                         
                    }                      
                }, (err) => {
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your image",
                        message: err,
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();                    
                })
      
                
            }, 
            err => {
                //console.log(err)
            });          
    }

    public updateImage(index){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
                
                let toast = this.toastCtrl.create({
                    message: 'Your image is being uploaded...',
                    duration: 3000,
                    position: 'bottom'
                });

                toast.present();                
                
                fileTransfer.upload(image, encodeURI(AppSettings.uploadUrl), options).then((data) => {
                    var response = JSON.parse(data.response);
                    if (response.success === true){
                        this.job.imagesArray[index] = response.data;
                    } 
                    else{
                        let alert = this.alertCtrl.create({
                            title: "Error",
                            subTitle: "There was a problem uploading your image",
                            message: JSON.stringify(response),
                            buttons: [
                                {
                                    text: 'Dismiss',
                                    role: 'cancel'
                                }
                            ]
                        });
                        alert.present();                         
                    }                      
                }, (err) => {
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your image",
                        message: err,
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();                    
                })
      
                
            }, 
            err => {
                //console.log(err)
            });         
    }    

    
}






