import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, Events, ModalController } from 'ionic-angular';

import * as moment from 'moment';

import { JobProvider } from '../../providers/job/job';

import { ReviewProvider } from '../../providers/review/review';

import { ProfilePage } from '../profile/profile';

import { CreateReviewModal } from '../../modals/create-review/create-review';

/**
 * Generated class for the JobPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-customer-job',
    templateUrl: 'customer-job.html',
})
export class CustomerJobPage {
    
    public job:any;
    public properties:any;
    constructor(public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController, private jobProvider: JobProvider, private reviewProvider: ReviewProvider, public events: Events, public modalCtrl: ModalController) {
        this.properties = {activeTab:"details"};
        this.job = navParams.data.job;
        this.job.imagesArray = JSON.parse(this.job.images);
        console.log(this.job.imagesArray);
        
    }
    
    public viewProfile(applicant){
        if (applicant && applicant.profile){
            applicant.profile.permission = "contractor";
            this.navCtrl.push(ProfilePage, {profile:applicant.profile});
        }
    }
    
    public acceptApplicant(ev, applicant){
        ev.stopPropagation();
        let alert = this.alertCtrl.create({
          title: 'Confirm',
          subTitle: 'Are you sure you want to accept?',
          message: 'Once accepted, the contractor will be given your job location and notified to mow your lawns at your preferred date and time.',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            {
              text: 'Yes',
              handler: () => {
                  applicant.status = "accepted";
                  this.job.status = "inprogress";
                  this.jobProvider.acceptJob(this.job.id, applicant.profile_id).then(() => {
                      this.events.publish("job:accepted", {job:this.job});
                  });
                  
              }
            }
          ]
        });
        alert.present();          
    }
    
    public declineApplicant(ev, applicant){
        ev.stopPropagation();
        let alert = this.alertCtrl.create({
          title: 'Confirm',
          subTitle: 'Are you sure you want to decline?',
          message: 'Once declined, the contractor will be notified that they did not get the job.',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            {
              text: 'Yes',
              handler: () => {
                  applicant.status = "declined"
                  this.jobProvider.declineJob(this.job.id, applicant.profile_id).then(() => {
                      this.events.publish("job:declined", {job:this.job});
                  });
                  
              }
            }
          ]
        });
        alert.present();        
    }
    
    public completeJob(){
        let alert = this.alertCtrl.create({
          title: 'Confirm',
          subTitle: 'Are you sure you want to complete this job?',
          message: 'Once completed, the contractor will be notified and the job will be moved to your completed jobs.',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            {
              text: 'Yes',
              handler: () => {
                  this.job.status = "completed";
                  setTimeout(() => {this.postCompleteJob();},500);
                  this.jobProvider.completeJob(this.job.id).then(() => {
                      this.events.publish("job:completed", {job:this.job});
                  });
                  
              }
            }
          ]
        });
        alert.present();          
    }  
    
    private postCompleteJob(){
      let alert = this.alertCtrl.create({
          title: 'Congratulations',
          subTitle: 'This job is now complete',
          message: 'Do you want to post a review about the contractor?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            {
              text: 'Yes',
              handler: () => {
                    this.createReview();
              }
            }
          ]
        });
        alert.present();         
    }  

    private createReview(){
        
        let profile = {};
        for (let application of this.job.applications){
            if (application.status === "accepted"){
                profile = application.profile;
            } 
        }       
        let modal = this.modalCtrl.create(CreateReviewModal, {job:this.job, profile:profile,permission:"customer"}); 
        modal.onDidDismiss(review => {
            if (review){
                console.log(review);
                this.reviewProvider.createReview(review).then(() => {
                    this.events.publish("job:reviewed", {job:this.job});
                    let alert = this.alertCtrl.create({
                        title: 'Review Submitted',
                        message: 'Thanks for submitting a review. It will be shown publicly on their profile.',
                        buttons: [
                          {
                            text: 'Dismiss',
                            role: 'cancel',
                            handler: () => {
                              console.log('Cancel clicked');
                            }
                          }
                        ]
                      });
                      alert.present();                      
                }); 
            }
        })        
        modal.present();             
    }

    
    public formatTime(dateTimeString){
        return moment(dateTimeString).format("dddd, MMMM Do YYYY, h:mm a");
    }    

    ionViewDidLoad() {
      console.log('ionViewDidLoad JobPage');
    }

}



