import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, ModalController,Platform, AlertController} from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ProfileProvider } from '../../providers/profile/profile';

import { EmailComposer } from '@ionic-native/email-composer';

import { LoginPage } from '../../pages/login/login';

import { UpdateRadiusModal } from '../../modals/update-radius/update-radius';

/**
 * Generated class for the SettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
    
    public user:any;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public events: Events, private emailComposer: EmailComposer, public modalCtrl: ModalController, public plt: Platform, private authProvider: AuthenticationProvider, private alertCtrl: AlertController, private profileProvider:ProfileProvider) {
        
        this.user = {permission:"customer"};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
            }
        })
        
    }
  
    public openRate(){
        if (this.plt.is("ios")){
            window.open("https://itunes.apple.com/", '_system');
        }
        else if (this.plt.is("windows")){
            window.open("https://www.microsoft.com", '_system');
        }
        else{
            window.open("https://play.google.com/", '_system');
        }
        
    }
    public openFollow(){
        window.open("https://www.facebook.com/", '_system');
    }
    
    public openEmail(){
        let email = {
          to: ["yonasbrhan@yahoo.com"],
          subject: 'Subject for your support query',
          body: 'Provide details about the support you require.',
          isHtml: true
        };        

        this.emailComposer.open(email);
    }
    
    public openUpdateRadius(){    
         let modal = this.modalCtrl.create(UpdateRadiusModal, {profile:this.user.profile}); 
        modal.onDidDismiss(profile => {
            if (profile){
                //update profile
                this.user.profile = profile;
                this.profileProvider.updateProfile(profile);
            }
        })        
        modal.present();         
    }
    
    
    public openChangePassword(){
        let alert = this.alertCtrl.create({
            title: 'Change Password',
            inputs: [           
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                },
                {
                    name: 'confirmPassword',
                    placeholder: 'Confirm Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.password && data.confirmPassword && data.password === data.confirmPassword){

                            
                            this.authProvider.changePassword(this.user.email, data.password, data.confirmPassword).then(() => {
                                let alert = this.alertCtrl.create({
                                    title: 'Success',
                                    message: 'Your password has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }); 
                                alert.present();                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    title: 'Error',
                                    message: 'There was an error while changing your password.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangePassword()},200);
                                            }
                                        }                               
                                    ]            
                                });
                                setTimeout(() => {alert.present()}, 200);                                 
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                title: 'Error',
                                message: 'Passwords do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangePassword()},200);
                                        }
                                    }                               
                                ]            
                            });
                            setTimeout(() => {alert.present()}, 200);                             
                        }
    
                    }
                }
                
            ]
        });
        alert.present();        
    }
    
    public logout(){
        this.authProvider.logout();
        this.navCtrl.setRoot(LoginPage);
        this.navCtrl.popToRoot();             
    }      

  ionViewDidLoad() {
    console.log('ionViewDidLoad SettingsPage');
  }

}



