import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';


/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NotificationProvider {

    constructor(public http: HttpClient, public storage: Storage) {
    }
  
    public getNotifications(){
        return new Promise((resolve, reject) => {
            this.storage.get("notifications").then((data:Array<any>) => {
                if (data){
                    resolve(data);
                }
                else{
                    resolve([]);
                }
            });
        });
    }
    
    public addNotification(notification){
        return new Promise((resolve, reject) => {
            this.storage.get("notifications").then((data:Array<any>) => {
                let notifications = [];
                if (data){
                    notifications = data;
                }

                if (notifications.length > 5){
                    notifications.pop();
                }

                notification.read = false;
                notifications.push(notification);

                this.storage.set("notifications", notifications).then(() => {
                    resolve();
                });
                
            })
        });        
    }
    
    public updateNotification(index){
        return new Promise((resolve, reject) => {
            this.storage.get("notifications").then((notifications:Array<any>) => {
                
                if (notifications && notifications[index]){
                    notifications[index].read = true;
                    this.storage.set("notifications", notifications).then(() => {
                        resolve();
                    });                    
                }



            })  
        });      
    }    
    
    public getNotificationCount(){
        return new Promise((resolve, reject) => {
            this.storage.get("notifications").then((notifications:Array<any>) => {
                let count = 0;
                
                if (notifications){
                
                    for(let notification of notifications){
                        if (!notification.read){
                            count +=1;
                        }
                    }
                }
                
                resolve(count);

            });
        });        
    } 
}



