import { Component } from '@angular/core';
import { NavController, ViewController, ModalController, Events } from 'ionic-angular';

import { NotificationProvider } from '../../providers/notification/notification';

import { JobProvider } from '../../providers/job/job';
import { ProfileProvider } from '../../providers/profile/profile';
import { Storage } from '@ionic/storage';

import { ContractorJobPage } from '../../pages/contractor-job/contractor-job';
import { CustomerJobPage } from '../../pages/customer-job/customer-job';
import { ProfilePage } from '../../pages/profile/profile';

@Component({
    selector: 'notifications-popover',
    templateUrl: 'popover.html'
})


export class PopoverPage {
    
    public properties:any;
    public notifications:Array<any>;
    public user:any;
    
    constructor(public nav: NavController, public viewCtrl: ViewController, public modalCtrl: ModalController, public notificationProvider:NotificationProvider, private storage:Storage, private jobProvider:JobProvider, private profileProvider:ProfileProvider, private events:Events) {
        this.properties = {};
     
        this.notifications = [];
        
        this.notificationProvider.getNotifications().then((data:Array<any>) => {
            this.notifications = data;
        });
        
        this.user = {};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
            }
        });  
        
        this.events.subscribe("notification:received", (notification) => {
            if (this.notifications.length > 5){
                this.notifications.pop();
            }

            notification.read = false;
            this.notifications.push(notification);            
            this.notificationProvider.addNotification(notification).then(() => {
                this.events.publish("notification:updatecount");
            }) 
        });      
                         
    }
    
    
    public viewNotification(index, notification){
        notification.read = true;
        
        this.notificationProvider.updateNotification(index).then(() => {
            this.events.publish("notification:updatecount");
        });   
        
        this.close();
         
        if (notification.type === "job" && this.user.permission === "contractor"){
            this.jobProvider.getJob(notification.id).then((job) => {
                this.nav.push(ContractorJobPage, {job:job});

            });
        }
        else if (notification.type === "job" && this.user.permission === "customer"){
            this.jobProvider.getJob(notification.id).then((job) => {
                this.nav.push(CustomerJobPage, {job:job});
            });
        }
        else if (notification.type === "profile"){
            if (notification.id){
            this.profileProvider.getProfile(notification.id).then((profile) => {
                this.nav.push(ProfilePage, {profile:profile});
            });
            }
            else{
                setTimeout(() => {this.nav.push(ProfilePage, {});},500);                            
            }
        }          
        

    }
    
 
    close() {
        this.viewCtrl.dismiss();
    }
}


