import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';


/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class JobProvider {

  constructor(public http: HttpClient, public storage: Storage) {
  }
  

    public createJob(job){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/jobrequests?token=" + token, job).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }
    
    public updateJob(job){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.put(AppSettings.apiUrl + "/jobrequests/" + job.id + "?token=" + token, job).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    } 
    
    public deleteJob(id){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.delete(AppSettings.apiUrl + "/jobrequests/" + id + "?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    
    public acceptJob(jobId,profileId){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/acceptjob?token=" + token, {job_id:jobId,profile_id:profileId}).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }   
    
    public declineJob(jobId,profileId){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/declinejob?token=" + token, {job_id:jobId,profile_id:profileId}).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    public completeJob(jobId){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/completejob?token=" + token, {job_id:jobId}).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }               
    
    public getCreatedJobs(){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + "/jobrequests?token=" + token).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }
    
    public getJobs(filters){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + "/jobrequests?token=" + token + "&pricelow=" + filters.priceLow + "&pricehigh=" + filters.priceHigh + "&orderby=" + filters.orderBy + "&orderdirection=" + filters.orderDirection).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    } 
    
    
    public getJob(id){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + "/jobrequests/" + id + "?token=" + token).subscribe((res) => {
                        resolve(res["job_request"]);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }     
    
    public apply(jobId,profileId){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/jobapplications?token=" + token, {job_id:jobId,profile_id:profileId}).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }       

}



