import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { Storage } from '@ionic/storage';


@Component({
    selector: 'filters',
    templateUrl: 'filters.html'
})

export class FiltersModal {
    
    public filters:any;
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, private toastCtrl: ToastController, public storage: Storage, private alertCtrl: AlertController, public events: Events) {
        this.filters = {};
        Object.assign(this.filters,params.data.filters);
        console.log(this.filters);
    }

    public update() {
        this.viewCtrl.dismiss(this.filters);
    }


    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






