import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { FindAddressModal } from '../../modals/find-address/find-address';

import {AppSettings} from '../../app/app.settings';

import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult } from '@ionic-native/native-geocoder';

import * as moment from 'moment';

import {
    GoogleMaps,
    GoogleMap,
    GoogleMapsEvent,
    GoogleMapOptions,
    CameraPosition,
    MarkerOptions,
    Marker
} from '@ionic-native/google-maps';

@Component({
    selector: 'update-radius',
    templateUrl: 'update-radius.html'
})

export class UpdateRadiusModal {
    public profile:any;
    public map:GoogleMap;
    constructor(public platform: Platform, public params: NavParams, public modalCtrl: ModalController, public viewCtrl: ViewController,public storage: Storage, private alertCtrl: AlertController, public events: Events, private toastCtrl: ToastController, private nativeGeocoder: NativeGeocoder) {
        this.profile = {};
        Object.assign(this.profile, this.params.data.profile);
        this.loadMap();
    }

    public update(){
        this.viewCtrl.dismiss(this.profile);
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }

    private  loadMap() {
        
        //set default lat and lng as auckland city
        //this.profile.latitude = -36.8626662;
        //this.profile.longitude = -174.7253866;    
        
        console.log("loading");
        let mapOptions: GoogleMapOptions = {
            camera: {
                target: {
                    lat: this.profile.latitude,
                    lng: this.profile.longitude
                },
                zoom: 11,
                tilt: 30
            }
        };

        this.map = GoogleMaps.create('map', mapOptions);
        
        
        this.map.one(GoogleMapsEvent.MAP_READY).then(() => {
            
            //add existing
            this.addMarker({lat:this.profile.latitude, lng:this.profile.longitude});
            
            this.map.addEventListener("click").subscribe((data) => {
                console.log(data);
                this.addMarker(data);
            })
        })
        
    }
    
    private addMarker(data){
        //set profile lat and lng
        
        this.profile.latitude = data.lat;
        this.profile.longitude = data.lng;
        
        
        //remove other markers
        this.map.clear();
        
        this.map.addMarker({
            icon: '#79b402',
            animation: 'DROP',
            position: {
              lat: this.profile.latitude,
              lng: this.profile.longitude
            }
        });        
        //add this marker
        //add radius circle
        
        this.map.addCircle({
            center: {lat: this.profile.latitude, lng: this.profile.longitude},
            radius: this.profile.radius * 100,
            strokeColor : '#79b402',
            strokeOpacity: 0.8,
            strokeWidth: 5,
            fillColor : '#79b402',
            fillOpacity: 0.35,            
        })
    }
    
    public updateRadius(){
        if (!this.map){return;}
        this.addMarker({lat:this.profile.latitude, lng:this.profile.longitude});
    }
    
}






