import { Directive, HostListener } from '@angular/core';
import { PopoverController } from 'ionic-angular';
import {PopoverPage} from '../notifications/popover';

@Directive({
    selector: '[notifications]'
})
export class NotificationsDirective {
    

    constructor(public popoverCtrl: PopoverController) {    }

    @HostListener('click', ['$event']) onClick(ev) {
        this.presentPopover(ev)
    }  
    
    
    public presentPopover(ev) {
        let popover = this.popoverCtrl.create(PopoverPage, {}, {cssClass: "notifications-popover"});
        popover.present({
            ev: ev
        });

    }  

}




