import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { SignupPage } from '../signup/signup';
import { ContractorJobsPage } from '../contractor-jobs/contractor-jobs';
import { CustomerJobsPage } from '../customer-jobs/customer-jobs';

import { AuthenticationProvider } from '../../providers/authentication/authentication';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-login',
    templateUrl: 'login.html',
})
export class LoginPage {
    
    public user:any;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public authProvider: AuthenticationProvider) {
        this.user = {email:"", password:"", error:"", loading:false};
    }

    ionViewDidLoad() {
      console.log('ionViewDidLoad LoginPage');
    }
  
    public login(){
        this.user.loading = true;
        this.user.error = "";
        console.log("here");
        this.authProvider.login(this.user).then((token) => {
            this.authProvider.getUserData().then((user) => {
                this.user.loading = false;
                if (user["permission"] === "contractor"){
                    this.navCtrl.setRoot(ContractorJobsPage);
                    this.navCtrl.popToRoot();
                }
                else{
                    this.navCtrl.setRoot(CustomerJobsPage);
                    this.navCtrl.popToRoot();                    
                }
            }).catch((e) => {
                this.user.loading = false;
                
              
                
            })
        }).catch((e) => {
        console.log(e);
            this.user.loading = false;
            
            
            if (e && e.status === 401){
                this.user.error = "Invalid credentials";
            }
            else if (e && e.status === 422){
                for (let error of e.error.error.errors){
                    this.user.error = this.user.error + error + " ";
                }
            }
            else{
                this.user.error = e.message;
            }              
        })
    }
    
    public signup(){
        this.navCtrl.push(SignupPage);
    }

}



