import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';

import { ReviewProvider } from '../../providers/review/review';

import { ProfileProvider } from '../../providers/profile/profile';

import * as moment from 'moment';

import { Storage } from '@ionic/storage';

import { EditProfileModal } from '../../modals/edit-profile/edit-profile';


/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
    
    public profile:any;
    public properties:any;
    constructor(public navCtrl: NavController, public navParams: NavParams, private reviewProvider: ReviewProvider, private profileProvider: ProfileProvider, public storage: Storage, public modalCtrl: ModalController) {
        this.properties = {activeTab:"profile"};
        this.profile = navParams.data.profile ? navParams.data.profile : {};
        this.profile.reviews = [];
        if (!this.profile.id){
            this.storage.get("user").then((user) => {
                if (user){
                    this.profile = user.profile;
                    this.profile.permission = user.permission;
                    this.profile.isMyProfile = true;
                    this.profile.reviews = [];
                    this.getReviews();
                }
            })
        }
        else{
            this.getReviews();            
        }

    }
    
    private getReviews(){
        this.reviewProvider.getReviews(this.profile.id).then((data) => {
            this.profile.reviews = data ? data : [];
        });
    }
    
    public formatDate(date){
        return moment(date).format("dddd, MMMM Do YYYY");
    }
    
    public openEditProfile(){
        let modal = this.modalCtrl.create(EditProfileModal, {profile:this.profile}); 
        modal.onDidDismiss(profile => {
            if (profile){
                //update profile
                this.profile = profile;
                this.profileProvider.updateProfile(profile);
            }
        })        
        modal.present();         
    }
    
    public openURL(url){
        window.open(url, '_system');
    }

    ionViewDidLoad() {
      console.log('ionViewDidLoad ProfilePage');
    }

}



