import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http';
import { IonicStorageModule } from '@ionic/storage';

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';
import { SignupPage } from '../pages/signup/signup';
import { ProfilePage } from '../pages/profile/profile';
import { ContractorJobsPage } from '../pages/contractor-jobs/contractor-jobs';
import { CustomerJobsPage } from '../pages/customer-jobs/customer-jobs';
import { CustomerJobPage } from '../pages/customer-job/customer-job';
import { ContractorJobPage } from '../pages/contractor-job/contractor-job';
import { SettingsPage } from '../pages/settings/settings';

import { FiltersModal } from '../modals/filters/filters';
import { CreateJobModal } from '../modals/create-job/create-job';
import { EditJobModal } from '../modals/edit-job/edit-job';
import { FindAddressModal } from '../modals/find-address/find-address';
import { CreateReviewModal } from '../modals/create-review/create-review';
import { EditProfileModal } from '../modals/edit-profile/edit-profile';
import { UpdateRadiusModal } from '../modals/update-radius/update-radius';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { OneSignal } from '@ionic-native/onesignal';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { GooglePlacesAutocompleteComponentModule } from 'ionic2-google-places-autocomplete';
import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult } from '@ionic-native/native-geocoder';
import { EmailComposer } from '@ionic-native/email-composer';

import {NotificationsDirective} from '../components/notifications/notifications';
import {PopoverPage} from '../components/notifications/popover';

import {AppSettings} from './app.settings';

import { AuthenticationProvider } from '../providers/authentication/authentication';
import { ProfileProvider } from '../providers/profile/profile';
import { JobProvider } from '../providers/job/job';
import { ReviewProvider } from '../providers/review/review';
import { NotificationProvider } from '../providers/notification/notification';

import {
 GoogleMaps
} from '@ionic-native/google-maps';

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    SignupPage,
    ProfilePage,
    ContractorJobsPage,
    CustomerJobsPage,
    CustomerJobPage,
    ContractorJobPage,
    SettingsPage,
    FiltersModal,
    CreateJobModal,
    EditJobModal,
    CreateReviewModal,
    FindAddressModal,
    EditProfileModal,
    UpdateRadiusModal,
    NotificationsDirective,
    PopoverPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
    GooglePlacesAutocompleteComponentModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    SignupPage,
    ProfilePage,
    ContractorJobsPage,
    CustomerJobsPage,
    CustomerJobPage,
    ContractorJobPage,
    SettingsPage,
    FiltersModal,
    CreateJobModal,
    EditJobModal, 
    CreateReviewModal,
    FindAddressModal,  
    EditProfileModal, 
    UpdateRadiusModal,
    PopoverPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    GoogleMaps,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthenticationProvider,
    ProfileProvider,
    JobProvider,
    ReviewProvider,
    NotificationProvider,
    FileTransfer,
    Camera,
    OneSignal,
    NativeGeocoder,
    EmailComposer        
  ]
})
export class AppModule {}



